.mod__coctail {
  @media screen and(max-width:767px) {
      padding: 40px 20px;
      position: relative;
    }
};

.mod__coctail--name {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 900;
    font-size: 32px;
    line-height: 1.19;
    
    color: #202025;

    margin-bottom: 20px;

    @media screen and(min-width:768px) {
        order: 1;
    position: absolute;
    top: 5%;
    left: 37%;
      }
};

.mod__coctail--btn__close {
  position: absolute;
  top: 16px;
  right: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-color: transparent;
  background-color: transparent;
}

.mod__coctail--blockinstractions--allingredients{
  @media screen and(min-width:768px) {
      display: flex;
      flex-wrap: wrap;
    }
}

.mod__coctail--btn__link {
  fill: $text-color;
}

.mod__coctail--blockinstractions{
@media screen and(min-width:768px) {
    order: 3;
    margin-left: 28px;
  }
}

.mod__coctail--instractions {
  font-family: 'Poppins';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 1.62;
  
    color: #202025;

  margin-bottom: 20px;
  text-transform: uppercase;

  
};

.mod__coctail--recipe {
  font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.75;
  
    color: #5F6775;

  margin-bottom: 32px;
  max-width: 480px;

  @media screen and(min-width:768px) {
  max-width: 615px;
    }
};

.mod__coctail--block__photo{
  margin-bottom: 32px;
  max-width: 480px;
  height: 280px;
  background-color: antiquewhite;

  @media screen and(min-width:768px) {
      width: 33%;
    }
}

.mod__coctail--allingredients{
@media screen and(min-width:768px) {
    order: 2;
    padding-top: 100px;
    padding-left: 32px;
    width: 66%;
  }
}

.mod__coctail--ingredients {
  font-family: 'Poppins';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 1.5;

    color: #000917;

  margin-bottom: 8px;
  text-transform: uppercase;
};

.mod__coctail--compound {
  font-family: 'Poppins';
    font-style: normal;
    font-weight: 300;
    font-size: 14px;
    line-height: 1.5;

    color: #202025;

  margin-bottom: 8px;
};

.mod__coctail--items{
  margin-bottom: 32px;
}

// .mod__coctail--item {

// };

.mod__coctail--item__link{
    font-family: 'Poppins';
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 1.62;
    
      color: #5F6775;
      
  &:hover,
  &:focus {
    color: antiquewhite;
  }
}

.mod__coctail--blockbutton--add{
  @media screen and(min-width:768px) {
      order: 4;
      margin-right: 28px;
      display: flex;
      justify-content: flex-end;
    }
}

.mod__coctail--button--add {
  font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 1.2;
    letter-spacing: 0.02em;
  
    color: #FFFFFF;

    background: #FE7031;
    border: #FE7031;
    border-radius: 4px;

  width: 185px;
  height: 48px;

  &:hover,
  &:focus {
      color: #FD5103;
      background: #F5F5F5;
    }
  &:active{
    color: #FFFFFF;
    background: #FD5103;
  }
};

.mod__coctail--blockbutton--remove {
  @media screen and(min-width:768px) {
    order: 4;
    margin-right: 28px;
    display: flex;
    justify-content: flex-end;
  }
}

.mod__coctail--button--remove {
  font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 1.2;
    letter-spacing: 0.02em;
  
    color: #FFFFFF;
  
    background: #FE7031;
    border: #FE7031;
    border-radius: 4px;
  
    width: 185px;
    height: 48px;
  
    &:hover,
    &:focus {
      color: #FD5103;
      background: #F5F5F5;
    }
  
    &:active {
      color: #FFFFFF;
      background: #FD5103;
    }
};


